@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://fonts.googleapis.com/css?family=Sacramento);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: 'Oswald', sans-serif;
}

h2,
.communityName{
  font-family: 'Sacramento', cursive;
  text-align: center;
}

.header{
  background-image: url(/apartmentsite/static/media/header.1bd1f4c1.jpg);
  height: 20em;
}

.communityName{
  padding-top: 125px;
  font-size: 6em;
  font-weight: 900;
  color: #0086b3;
  text-shadow:
  -1px -1px 0 #ccc,  
   1px -1px 0 #ccc,
   -1px 1px 0 #ccc,
    1px 1px 0 #ccc;
}

.navbar-brand{
  font-family: 'Sacramento', cursive;
  font-size: 2em;
}

.community,
.communityDescription{
  padding-top: 15px;
  width: 80vw;
  margin: auto;
  font-size: 1.25em;
  }

  .communityImage{
    width: 100%;
  }

  h2{
    font-size: 5em;
    font-weight: 600;
    text-align: center;
  }

  .map{
    width: 100%;
  }

  .unitDescriptions{
    width: 90vw;
    margin: auto;
  }

  .bottomButtons{
    font-size: 2em;
    margin-top: 15px;
    margin-right: 0px;
    justify-content: center;
  }

  .contact,
  .apply{
    border: 1px solid rgb(0, 134, 179);
    border-radius: 15px;
    padding: 10px;
    cursor: pointer;
    margin: 15px;
  }

  footer{
    font-family: 'Sacramento', cursive;
    font-size: .75em;
    padding: 15px;
    text-align: center;
  }
